body {
  font-size: 120%;
}

.card {
  color: #fff;
  width: 100%;
  max-width: 420px;
  background: #000000d0;
  border-radius: 30px;
  margin: 1em;
  padding: 2em;
}

.search {
  justify-content: center;
  align-items: center;
  display: flex;
}

button {
  height: 44px;
  width: 44px;
  color: #fff;
  cursor: pointer;
  background: #7c7c7c2b;
  border: none;
  border-radius: 50%;
  outline: none;
  margin: .5em;
  transition: all .2s ease-in-out;
}

input.search-bar {
  color: #fff;
  width: calc(100% - 100px);
  background: #7c7c7c2b;
  border: none;
  border-radius: 24px;
  outline: none;
  padding: .4em 1em;
  font-family: inherit;
  font-size: 105%;
}

button:hover {
  background: #7c7c7c6b;
}

h1.temp {
  margin: 0 0 .4em;
}

.flex {
  align-items: center;
  display: flex;
}

.description {
  text-transform: capitalize;
  margin-left: 8px;
}

.weather.loading {
  visibility: hidden;
  max-height: 20px;
  position: relative;
}

.weather.loading:after {
  visibility: visible;
  content: "Loading...";
  color: #fff;
  position: absolute;
  top: 0;
  left: 20px;
}

.info_text {
  margin: 20px 0 0;
  display: none;
}

.info_text.pending {
  color: #ffd724e6;
  display: block;
}

.info_text.error {
  color: #ff4824e6;
  display: block;
}

